import fetch from 'isomorphic-unfetch';
import Link from 'next/link';
import { request } from 'graphql-request';

const Index = props => (
  <div>
    <h1>Title</h1>
    <ul>
      {props.res.allRoutes.map(route => (
        <li key={route.id}>
          <Link as={`/r/${route.id}`} href={`/post?id=${route.id}`}>
            <a>{route.routeName}</a>
          </Link>
        </li>
      ))}
    </ul>
  </div>
);

// Index.getInitialProps = async function() {
