//   const data = await res.json();
//   console.log(`Show data fetched. Count: ${data.length}`);
//   return { shows: data };
// };

Index.getInitialProps = async function() {
  const query = `query {
    allRoutes {
      id
      routeName
    }
  }`;
  const res = await request('https://server-dubymzzcjd.now.sh/graphql', query);
  console.log(`Route data fetched. Count: ${res.allRoutes.length}`);
  return { res };
};

export default Index;



